.news-news {
  &__main-footer {
    margin-top: 8px;
    ul {
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      margin: 0;
      li {
        margin-right: 8px;
        margin-bottom: 8px;
        &:last-child {
          margin-bottom: 8px;
        }
        a {
          padding: 0 12px;
          p {
            margin-left: 4px;
          }
          svg {
            margin-right: 4px;
          }
        }
        .btn-global--social {
          padding: 0;
          width: 32px;
        }
      }
    }
    &-wrap {
      display: flex;
      justify-content: space-between;
      margin-top: 24px;
      margin-bottom: 12px;
    }
    &-like {
      flex-wrap: nowrap;
      li {
        a {
          &:hover {
            color: $green;
            svg path {
              fill: transparent;
              stroke: $green;
            }
          }
        }
        &:last-child {
          a {
            svg {
              transform: scale(1, -1);
            }
            &:hover {
              color: $red;
              svg path {
                stroke: $red;
              }
            }
          }
        }
      }
    }
    &-text {
      font-size: 12px;
      line-height: 14px;
      letter-spacing: 0.3px;
      color: $grey3;
      text-decoration: none;
    }
  }
  &__news-list li {
    width: 32%;
  }
  & .matches__top,
  & .news-news__news-list {
    margin-left: 15px;
    margin-right: 15px;
  }
  &__block-comment {
    background: $black1;
    border-radius: $borderRadius4;
    padding: 14px 20px;
    padding-bottom: 55px;
    &-top {
      display: flex;
      align-items: flex-start;
      padding-bottom: 14px;
      h2 {
        margin: 0;
      }
      div {
        display: flex;
        margin-left: 44px;
        a {
          margin-left: 0;
          margin-right: 12px;
          height: 28px;
          padding: 0 8px;
        }
      }
      a {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 28px;
        padding: 0 8px;
        margin-left: auto;
        font-size: 13px;
        letter-spacing: 0.25px;
        color: $grey2;
        svg {
          margin-right: 4px;
        }
        &:hover {
          color: $white;
          svg path {
            fill: $white;
          }
        }
      }
    }
  }
}

@media (max-width: 680px) {
  .news-news {
    &__main-footer {
      &-wrap {
        flex-direction: column;
      }
    }
    &__news-list {
      .index__news-item {
        width: 48%;
        &:nth-child(3) {
          display: none;
        }
      }
    }
    &__block-comment {
      padding: 14px 15px 55px;
      &-top {
        flex-wrap: wrap;
        align-items: center;
        div {
          order: 1;
          margin: 0;
          margin-top: 10px;
          width: 100%;
        }
      }
    }
  }
}