.base-hero {
  &__btn-block {
    display: flex;
    align-items: center;
    margin-top: 20px;
    a {
      margin-right: 8px;
    }
  }
  &__filter {
    display: flex;
    flex-wrap: wrap;
    margin-top: 24px;
    &-span {
      width: 100%;
    }
    input,
    select {
      height: 40px;
      background: $black2;
      border: 1px solid $black1;
      box-sizing: border-box;
      border-radius: $borderRadius4;
      padding: 0 12px;
      font-size: 15px;
      line-height: 150%;
      letter-spacing: 0.5px;
      color: $grey2;
      margin-top: 8px;
      margin-right: 8px;
      &::placeholder {
        color: inherit;
      }
    }
  }
  &__subtitle {
    background: $black2;
    border-radius: $borderRadius4;
    &:hover {
      color: $grey0;
    }
    img {
      width: 33px;
      border-radius:$borderRadius50;
      margin-right: 8px;
    }
  }
  &__block {
    margin-top: 24px;
  }
  &__block-hero {
    width: calc(100% + 8px);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    margin-left: -4px;
  }
  &__link-hero {
    position: relative;
    display: flex;
    width: 83px;
    height: 50px;
    margin: 0 4px;
    margin-top: 8px;
    opacity: 0.8;
    img {
      width: 100%;
    }
    &-name {
      display: none;
    }
    &:hover {
      opacity: 1;
      & .base-hero__link-hero-name {
        position: absolute;
        top: 100%;
        left: calc(50% - 60px);
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        background: $black2;
        border-radius: $borderRadius4;
        padding: 9px 10px;
        width: 120px;
        z-index: 2;
      }
    }
  }
}