.news-page {
  &__content {
    .index__news-list {
      margin-top: 0;
      margin-top: 24px;
    }
    .index__news-item {
      width: 32%;
    }
  }
  &__list {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin-top: 15px;
    overflow: hidden;
  }
  &__item {
    margin-right: 8px;
    margin-bottom: 8px;
    &--active a {
      background: $black3;
      color: $white
    }
    &:last-child {
      margin-right: 0
    }
  }
  &__search {
    display: flex;
    width: 100%;
    border-radius: $borderRadius4;
    margin-top: 24px;
    input,
    button {
      height: 40px;
      border: none;
    }
    input {
      width: calc(100% - 40px);
      padding: 5px 12px;
      background: $black2;
      border-radius: $borderRadius4 0px 0px $borderRadius4;
      font-size: 13px;
      color: $grey1;
      &:hover {
        background: $black3;
      }
      &::placeholder {
        color: $grey2;
      }
    }
    button {
      width: 40px;
      background: $black2;
      border-radius: 0px $borderRadius4 $borderRadius4 0px;
      &:hover {
        background: $black3;
        svg path {
          fill: $white;
        }
      }
    }
  }
}

@media (max-width: 1023px) {
  .news-page {
    .index__news-list {
      margin-left: 0;
      margin-right: 0;
    }
    .index__news-item {
      width: 48%;
    }
  }
}

@media (max-width: 940px) {
  .news-page {
    .index__news-item {
      width: 32%;
    }
  }
}

@media (max-width: 800px) {
  .news-page {
    .index__news-item {
      width: 48%;
    }
  }
}