.base-items {
  &__block-shop {
    margin-top: 24px;
    display: none;
    align-items: flex-start;
    justify-content: space-between;
  }
  &__block-input {
    display: flex;
    align-items: center;
    width: 100%;
    margin-top: 24px;
    border-radius: $borderRadius4;
    overflow: hidden;
    label {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 50%;
      height: 56px;
      background: $black2;
      cursor: pointer;
    }
  }
  &__shop {
    &-list {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      padding: 8px;
      border-radius: $borderRadius4;
      background: $black2;
      &:nth-child(n+6) .base-items__shop-descr {
        display: none;
      }
    }
    &-item {
      position: relative;
      display: flex;
      margin-top: 8px;
      &:first-child {
        margin-top: 0
      }
    }
    &-link {
      display: flex;
      &:hover ~ .base-items__shop-descr {
        display: flex;
      }
    }
    &-img {
      width: 50px;
      height: 30px;
      border-radius: $borderRadius4;
    }
    &-descr {
      position: absolute;
      display: none;
      flex-direction: column;
      width: 537px;
      top: 100%;
      left: -8px;
      padding: 15px;
      background: $black2;
      border-radius: $borderRadius4;
      z-index: 2;
      & > div {
        margin-top: 16px;
        &:first-child {
          margin-top: 0;
        }
      }
      &-wrap {
        display: flex;
        align-items: center;
        img {
          width: 100px;
          height: 70px;
          border-radius: $borderRadius4;
          margin-right: 8px;
        }
        p {
          display: flex;
          align-items: center;
          font-size: 20px;
          img {
            width: 30px;
            height: 20px;
          }
        }
      }
      &-special {
      }
      &-descr {
      }
      &-charact {
      }
      &-stat {
      }
      &-mana {
        display: flex;
        align-items: center;
        padding: 8px 10px;
        border-radius: $borderRadius4;
        background: $black3;
        &-left,
        &-right {
          display: flex;
          align-items: center;
          img {
            width: 20px;
            height: 20px;
            border-radius: $borderRadius4;
            margin-right: 8px;
          }
        }
        &-left {
          margin-right: auto;
        }
        &-right {
          margin-left: auto;
        }
      }
      &-comp {
        h4 {
          margin: 0;
        }
        ul {
          display: flex;
          align-items: flex-start;
          margin-top: 8px;
          li {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            margin-right: 8px;
            img {
              width: 50px;
              height: 30px;
              border-radius: $borderRadius4;
              margin-bottom: 3px;
            }
            &:last-child {
              margin-right: 0;
            }
          }
        }
      }
    }
  }
  &__block-neutral {
    display: none;
    flex-direction: column;
    &-elem {
      display: flex;
      flex-direction: column;
      padding: 10px 15px;
      background: $black2;
      border-radius: $borderRadius4;
      margin-top: 15px;
    }
    &-descr {
      display: flex;
      justify-content: space-between;
      align-items: center;
      h4 {
        padding: 0;
        height: auto;
        &:hover {
          color: $grey0;
        }
      }
    }
    &-list {
      display: flex;
      justify-content: center;
      flex-wrap: wrap;
      margin-top: 8px;
      li {
        margin: 4px;
        &:first-child {
          margin: 4px;
        }
        .base-items__shop-img {
          width: 63px;
          height: 46px;
        }
      }
    }
  }
  &__shop-list:nth-child(n+7) .base-items__shop-descr {
    left: auto;
    right: -8px;
  }
  &__block-neutral-list .base-items__shop-descr {
    width: 460px
  }
}

#radio-shop:checked ~ .base-items__block-input label[for="radio-shop"],
#radio-neutral:checked ~ .base-items__block-input label[for="radio-neutral"] {
  background: $black3;
}
#radio-shop:checked ~ .base-items__block-shop,
#radio-neutral:checked ~ .base-items__block-neutral {
  display: flex;
}

@media (max-width: 1023px) {
  .base-items {
    &__block-shop {
      flex-direction: column;
    }
    &__shop {
      &-list {
        flex-direction: row;
        align-items: center;
        justify-content: flex-start;
        flex-wrap: wrap;
      }
      &-item {
        margin-top: 0;
        margin-right: 8px;
        margin-bottom: 8px;
        &:last-child {
          margin-right: 0
        }
      }
      &-link {
        &:hover ~ .base-items__shop-descr {
          display: none;
        }
      }
    }
  }
}

@media (max-width: 540px) {
  .base-items {
    &__block-neutral {
      &-descr {
        flex-direction: column;
        justify-content: center;
      }
    }
  }
}