.global-content {
  h2,
  h3  {
    font-weight: 500;
    font-size: 20px;
    line-height: 140%;
    letter-spacing: 0.15px;
    color: $grey0;
    margin: 0;
    margin-bottom: 20px
  }
  h3 {
    font-size: 17px;
    line-height: 135%;
    letter-spacing: 0.25px;
    margin-top: 32px;
    margin-bottom: 8px
  }
  &__header {
    p > a {
      color: $grey2;
      text-decoration: underline;
      &:hover {
        color: $white;
        text-decoration: none;
      }
    }
    img {
      width: 100%;
      margin-top: 32px;
    }
  }
  &__lists {
    ol,
    ul {
      padding: 0;
      margin: 0;
      margin-bottom: 32px;
      li {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        margin-bottom: 4px;
        &:last-child {
          margin-bottom: 0;
        }
        a {
          color: $grey2;
          text-decoration: underline;
          &:hover {
            color: $white;
            text-decoration: none;
          }
        }
        p,
        a {
          display: flex;
          align-items: center;
          margin-right: 8px;
          min-height: 23px;
          img {
            display: flex;
            width: 16px;
            margin: 0;
            margin-right: 4px;
          }
        }
      }
    }
    ol {
      list-style-type: none;
      counter-reset: num;
      li::before {
        content: counter(num) '. ';
        counter-increment: num;
        font-size: 15px;
        line-height: 150%;
        letter-spacing: 0.5px;
        color: $grey2;
      }
      a {
        margin-left: 5px;
      }
    }
    ul {
      li {
        position: relative;
        padding-left: 10px;
        flex-wrap: nowrap;
        align-items: flex-start;
        &::before {
          content: '';
          position: absolute;
          top: 10px;
          left: 0;
          width: 4px;
          height: 4px;
          border-radius: $borderRadius50;
          background: $grey2;
        }
      }
    }
  }
  &__quote {
    blockquote {
      padding: 28px;
      margin: 0;
      background: $gradientHover;
      border-radius: $borderRadius4;
      & > div {
        display: flex;
        align-items: flex-start;
        p {
          font-size: 17px;
          margin-bottom: 15px;
          &:last-child {
            margin-bottom: 0;
          }
        }
      }
      img {
        width: 40px;
        margin-right: 12px;
      }
      & > div:last-child {
        display: flex;
        align-items: flex-start;
        margin-top: 16px;
        color: $grey2;
        p {
          font-size: 13px;
        }
      }
    }
  }
  &__interview {
    & > p {
      font-size: 13px;
      letter-spacing: 0.25px;
      color: $grey2;
      a {
        color: $grey2;
        &:hover {
          color: $white;
        }
      }
    }
    dl {
      margin: 0;
      margin-top: 20px;
      dt,
      dd {
        position: relative;
        margin-left: 0;
        padding-left: 24px;
        &:before {
          content: '';
          position: absolute;
          top: 11px;
          left: 0;
          width: 16px;
          height: 1px;
          background: $grey3;
        }
      }
      dt {
        margin-bottom: 8px;
        p {
          font-weight: 500;
          font-size: 17px;
          line-height: 135%;
          letter-spacing: 0.25px;
          color: $grey0;
        }
      }
      dd {
        margin-bottom: 20px;
        p {
          margin-bottom: 16px;
          &:last-child {
            margin-bottom: 0;
          }
        }
      }
    }
  }
  &__tabel {
    p {
      font-size: 13px;
      letter-spacing: 0.25px;
      color: $grey2;
      margin-top: 20px;
      margin-bottom: 12px;
    }
    &-version-1,
    &-version-2 {
      background: $black2;
      width: 100%;
      border-collapse: collapse;
      th,
      td {
        border: 1px solid $black1;
      }
    }
    &-version-1 {
      th,
      td {
        height: 44px;
        text-align: center;
      }
      th {
        background: $black3;
        font-weight: bold;
      }
      tr:hover {
        background: $black3;
      }
    }
    &-version-2 {
      td {
        padding: 30px;
        b {
          display: flex;
          align-items: center;
          justify-content: center;
        }
        ul {
          margin-top: 12px;
          li {
            margin-bottom: 4px;
            position: relative;
            padding-left: 10px;
            &::before {
              content: '';
              position: absolute;
              top: 10px;
              left: 0;
              width: 4px;
              height: 4px;
              border-radius: $borderRadius50;
              background: $grey2;
            }
            &:last-child {
              margin-bottom: 0;
            }
            a {
              display: flex;
              align-items: center;
              img {
                width: 16px;
                margin-right: 4px;
              }
            }
          }
        }
      }
    }
  }
  &__slider {
    &-top {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      height: 410px;
      max-height: 410px;
      background: $black0;
      border-radius: $borderRadius4 $borderRadius4 0 0;
      div {
        display: flex;
        align-items: center;
        justify-content: center;
        img {
          width: 100%;
          max-height: 410px;
        }
      }
      button {
        position: absolute;
        top: calc(50% - 20px);
        width: 40px;
        height: 40px;
        background: $black2;
        opacity: 0.7;
        border-radius: $borderRadius4;
        &:hover {
          opacity: 1;
        }
      }
    }
    &-btn--left {
      left: 20px;
    }
    &-btn--right {
      right: 20px;
      transform: rotate(180deg);
    }
    &-bottom {
      display: flex;
      align-items: flex-start;
      background: $black2;
      border-radius: 0 0 $borderRadius4 $borderRadius4;
      padding: 20px;
      span {
        margin-right: 20px;
        color: $grey3;
      }
      p {
        font-size: 13px;
      }
    }
  }
  &__poll {
    &-wrap {
      display: flex;
      justify-content: space-between;
      div {
        padding: 20px 32px;
        width: 48%;
        display: flex;
        flex-direction: column;
        background-image: $gradientMatch, url(../img/news-news/img-slider.jpg);
        background-repeat: no-repeat;
        background-size: cover;
        border-radius: $borderRadius4;
        margin-bottom: 24px;
        h3,
        p {
          margin: 0;
          text-align: center;
          margin: 0 auto;
        }
        p {
          font-size: 13px;
          letter-spacing: 0.25px;
          color: $grey2;
          margin-top: 12px;
        }
        label {
          display: flex;
          align-items: center;
          margin-top: 12px;
          height: 38px;
          padding: 0 12px;
          border-radius: $borderRadius4;
          background-color: $black1;
          cursor: pointer;
          font-size: 13px;
          letter-spacing: 0.25px;
          color: #C9C9CB;
          span {
            display: none;
            margin-left: 4px;
          }
          svg {
            display: none;
            margin-left: auto;
          }
          &:hover {
            background-image: $gradientHover;
            svg {
              display: block;
            }
          }
        }
        input:checked ~ label {
          color: $white;
          background-image: linear-gradient(90deg, #28282D 0%, #28282D 46%, rgba(0, 0, 0, 0) 46%, rgba(0, 0, 0, 0) 100%);
        }
        input:checked ~ label span {
          display: block;
          color: $white;
        }
        #global-content-radio-1:checked ~ label[for=global-content-radio-1],
        #global-content-radio-2:checked ~ label[for=global-content-radio-2],
        #global-content-radio-3:checked ~ label[for=global-content-radio-3],
        #global-content-radio-4:checked ~ label[for=global-content-radio-4],
        #global-content-radio-5:checked ~ label[for=global-content-radio-5],
        #global-content-radio-6:checked ~ label[for=global-content-radio-6] {
          background-image: linear-gradient(90deg, #39393E 0%, #39393E 46%, rgba(0, 0, 0, 0) 46%, rgba(0, 0, 0, 0) 100%), $gradientHover;
          svg {
            display: block;
            path {
              fill: $white;
            }
          }
        }
      }
    }
  }
  &__header,
  &__lists,
  &__quote,
  &__interview,
  &__tabel,
  &__slider {
    margin-bottom: 32px;
  }
  &__comment {
    & > & {
      position: relative;
      padding-left: 40px;
      margin-left: 12px;
      & > a {
        position: absolute;
        top: 0;
        left: 0;
        width: 40px;
        height: 100%;
        &::before {
          content: '';
          position: absolute;
          width: 2px;
          height: 100%;
          left: 50%;
          top: 0%;
          bottom: 0%;
          background: $black4;
        }
        &:hover::before {
          background: $gradientRed;
        }
      }
    }
    &-message {
      display: flex;
      align-items: flex-start;
      padding: 12px;
      border-radius: $borderRadius4;
      margin-bottom: 4px;
      &--grey {
        background: $gradientHover;
      }
    }
    &-wrap {
      margin-left: 12px;
      width: 100%;
      & > b {
        position: relative;
        display: block;
        padding-left: 10px;
        font-size: 12px;
        letter-spacing: 0.3px;
        color: $grey2;
        margin: 6px 0;
        max-height: 40px;
        &:before {
          content: '';
          position: absolute;
          top: -5px;
          left: 0;
          width: 2px;
          height: calc(100% + 10px);
          background: $black4;
        }
      }
      & > p {
        margin-top: 2px;
      }
      & > a {
        font-size: 12px;
        color: $grey2;
        &:hover {
          color: $white;
        }
      }
    }
    &-block-mobile {
      display: none;
      align-items: center;
      justify-content: space-between;
      flex-wrap: wrap;
      margin-top: 5px;
      p, a , div {
        display: flex;
        align-items: center;
        font-size: 12px;
        color: $grey2;
        svg {
          margin-right: 4px;
        }
      }
      a:hover {
        color: $white;
        svg path {
          fill: $white;
        }
      }
      div {
        margin-left: auto;
      }
    }
    &-user-img {
      display: flex;
      max-width: 40px;
      min-width: 40px;
      border-radius: $borderRadius50;
      img {
        width: 100%;
      }
    }
    &-name {
      font-weight: bold;
      font-size: 13px;
      margin-right: 4px;
    }
    &-reply {
      font-size: 12px;
      margin-right: 16px;
    }
    &-top {
      display: flex;
      align-items: center;
      justify-content: space-between;
      &-wrap {
        display: flex;
        align-items: center;
        p {
          display: flex;
          align-items: center;
          font-size: 13px;
          letter-spacing: 0.25px;
          color: $grey2;
          margin-right: 16px;
          svg {
            margin-right: 4px;
          }
        }
        a {
          display: flex;
          align-items: center;
          color: $grey2;
          &:hover {
            color: $white;
            svg path {
              fill: $white;
            }
          }
        }
        div {
          display: flex;
          align-items: center;
        }
      }
    }
    &-like {
      display: flex;
      align-items: center;
      a {
        display: flex;
        align-items: center;
        justify-content: center;
        margin-right: 4px;
        &:hover svg path {
          stroke: $green;
        }
        &:nth-child(2) {
          transform: scale(1, -1);
          &:hover svg path {
            stroke: $red;
          }
        }
      }
      span {
        font-size: 13px;
        letter-spacing: 0.25px;
        color: $grey2;
      }
      &.global-content__color-green span {
        color: $green;
      }
      &.global-content__color-red span {
        color: $red;
      }
    }
  }
}

@media (max-width: 900px) {
  .global-content {
    &__quote {
      blockquote {
        padding: 15px;
      }
    }
  }
}

@media (max-width: 680px) {
  .global-content {
    &__quote {
      blockquote {
        div {
          display: block;
        }
        img {
          float: left;
          margin-top: 10px;
        }
      }
    }
    &__tabel {
      &-version-2 {
        td {
          display: flex;
          flex-direction: column;
          ul {
            margin-left: 0;
          }
        }
      }
    }
    &__slider {
      &-top {
        height: auto;
        &-btn--left {
          left: 10px;
        }
        &-btn--right {
          right: 10px;
        }
      }
      &-bottom {
        padding: 15px 10px;
      }
    }
    &__poll {
      &-wrap {
        flex-direction: column;
        div {
          width: 100%;
          padding: 15px 10px;
        }
      }
    }
    &__comment {
      & > & {
        padding-left: 26px;
        margin-left: 0px;
        & > a {
          width: 26px;
          &::before {
            left: calc(50% - 1px);
          }
        }
      }
      &-message {
        padding-left: 6px;
        padding-right: 6px;
      }
      &-top {
        margin: 13px 0;
        &-wrap {
          .global-content__comment-reply,
          div,
          p {
            display: none;
          }
        }
      }
      &-block-mobile {
        display: flex;
      }
      &-block-mobile,
      &-wrap > b,
      &-wrap > p,
      &-wrap > a {
        margin-left: -52px;
      }
    }
  }
}